<template>
  <div class="">
    <div class="liveNav">
      <div class="item active">精选</div>
      <div class="item">商业财经</div>
      <div class="item">男女交际</div>
      <div class="item">文化讲堂</div>
      <div class="item">艺术海洋</div>
      <div class="item">技能掌握</div>
      <div class="item">法律课程</div>
      <div class="item">养生观念</div>
      <div class="item">畅游国际</div>

    </div>
    <el-carousel :interval="5000" arrow="always" height="200px">
      <el-carousel-item v-for="item in 4" :key="item">
        <img src="../../../static/img/home/Advertisement.png" style="width: 100%;height: 200px;">
      </el-carousel-item>
    </el-carousel>

    <div class="main1 book   bgfff" style="padding-top: 15px">
      <div class="list" style="width: 100%;overflow: hidden" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div class="item flex"  v-for="(item,index) in list" :key="index" @click="go({path: '/play'})">
          <div class="flex1">
            <img src="../../../static/img/home/Advertisement.png" style="width: 100%;height: auto">
          </div>
          <div class="flex2">
            <div class="title" style="font-size: 16px;;margin-top: 5px">《差异化营销》</div>
            <div style="line-height: 30px;color: RGBA(160, 160, 160, 1);font-size: 12px;font-family: PingFang-SC-Medium">
              2018.6.22发布
            </div>
            <div class="bottom" style="position: absolute;bottom: 0;width: 100%">
              <div style="font-size: 12px;color: #7D7D7D;">
                圆梦学海图书音像专营店 北京
              </div>
            </div>


          </div>
        </div>
      </div>
    </div>

    <div class="pageLoad" v-if="pageLoad">
      <i class="el-icon-loading"></i>
      上划自动加载
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        list:[1,2,3,4],
        pageLoad:false
      }
    },
    methods:{
      go(e){
        this.$router.push(e)
      },
      loadMore() {
        this.pageLoad = true;
        setTimeout(() => {
          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 2; i++) {
            this.list.push(last + i);
          }
          this.pageLoad = false;
        }, 2500);
      }
    },
    mounted(){

    }
  }
</script>


<style scoped>

</style>



